.radioButton {
	display: flex;
	align-items: center;
	position: relative;
	font-size: 0.8rem;
	padding-left: 42.5px;
	z-index: 5;
	.input {
		position: absolute;
		cursor: pointer;
		opacity: 0;
		left: -999%; 
		z-index: 5;
		&:checked {
			~ .radio {
			  background-color: #F9B300;
			  transition: background .3s;
			  &::after {
				opacity: 1;
			  }
			}
		}
	}
	.radio {
		position: absolute;
		cursor: pointer;
		top: 0; bottom: 0;
		margin: auto;
		left: 0;
		height: 25px;;
		width: 25px;
		background: #E4E4E4;
		border-radius: 50%;
	}
}


@media (max-width: 1400px) {
	.radioButton {
		padding-left:  calc(25px + (30 - 25)*((100vw - 1000px)/(1400 - 1000)));
		font-size: calc(0.7rem + (0.8 - 0.7)*((100vw - 1400px)/(1400 - 1000)));;
		.radio {
			height: 17px; width: 17px;
		}
	}
}

@media (max-width: 780px) {
	.radioButton {
		padding-left: calc(30px + (50 - 30)*((100vw - 400px)/(780 - 400)));
		font-size: calc(0.7rem + (0.8 - 0.7)*((100vw - 400px)/(780 - 400)));
		.radio {
			height: calc(20px + (32 - 20)*((100vw - 400px)/(780 - 400)));width: calc(20px + (32 - 20)*((100vw - 400px)/(780 - 400)));
		}
	}
}